footer {
    background: rgba(255, 255, 255, 0.8);
    padding-top: 2rem;
    padding-bottom: 5rem;
    text-align: center;
    font-size: 0.8rem;
    border-radius: 60px 60px 0 0;
    margin-top: 5rem;
}

footer a {
    color: rgba(0, 0, 0, 0.7);
}

.footer__logo {
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;
}

.permalinks {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    margin: 0 auto 3rem;
}

.footer__socials {
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 1rem;
}

.footer__socials a {
    background: var(--background);
    color: black;
    padding: 0.8rem;
    border-radius: 0.8rem;
    display: flex;
    border: 1px solid transparent;
}

.footer__socials a:hover {
    background: transparent;
    color: black;
    border-color: var(--background);
}

.footer__copyright {
    margin-bottom: 2rem;
    color: var(--lightWhite);
}

@media screen and (max-width:600px) {
    .permalinks {
        flex-direction: column;
        gap: 1.5rem;
    }

    .footer__socials {
        margin-bottom: 2.6rem;
    }
}