nav {
    display: flex;
    background: rgba(0, 0, 0, 0.3);
    width: max-content;
    padding: 0.2rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    gap: 0.5rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
}

nav a {
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: var(--lightWhite);
    font-size: 1.1rem;
}

nav a :hover {
    background: rgba(0, 0, 0, 0.3);
}

nav a.active {
    background: var(--primary);
    color: var(--background);
}