@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');

* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root {
    --background: #EAE7DC;
    --backgroundVariant: #D8C3A5;
    --primary: #E98074;
    --primaryVariant: #8E8D8A;
    --lightWhite: rgba(0, 0, 0, 0.6);
    --transition: all 400ms ease;
    --width-sm: 90%;
    --width-md: 86%;
    --width-lg: 75%;
}

html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    display: none;
}

body {
    font-family: 'Poppins', sans-serif;
    background: var(--background);
    background-size: cover;
    color: white;
    line-height: 1.7;
}

.container {
    width: var(--width-lg);
    margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5 {
    font-weight: 500;
}

h1 {
    font-size: 2.5rem;
}

section {
    margin-top: 8rem;
}

section>h2,
section>h5 {
    color: var(--lightWhite);
    text-align: center;
}

section>h2 {

    color: var(--primary);
    margin-bottom: 3rem;
}

.text-light {
    color: var(--lightWhite);

}

a {
    color: var(--primary);
    transition: var(--transition);
}

a:hover {
    color: white;
}

.btn {
    width: max-content;
    display: inline-block;
    color: var(--primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.5rem;
    cursor: pointer;
    border: 1px solid var(--primary);
    transition: var(--transition);
}

.btn:hover {
    background: var(--backgroundVariant);
    color: rgba(0, 0, 0, 0.7);
    border-color: rgba(0, 0, 0, 0.7);
}

.btn-primary {
    background: var(--primary);
    color: var(--background);
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
}

.kindOfGray {
    color: rgba(0, 0, 0, 0.7);
}

.primaryColor {
    color: var(--primary)
}

@media screen and (max-width:1024px) {
    .container {
        width: var(--width-md);

    }

    section {
        margin-top: 6rem;
    }
}

@media screen and (max-width:600px) {
    .container {
        width: var(--width-sm);

    }

    section>h2 {
        margin-bottom: 2rem;
    }
}